:root {
  --main-color: #212121;
}

body{
  text-align: left;
}

.App {
  font-family: sans-serif;
}

.menu-cnt {
  text-align: left;
}
.menu-btn {
  float: left;
}
.menu-btn-child {
  width: 30px;
  height: 5px;
  background-color: #212121;
  background-color: var(--main-color);
  margin-bottom: 5px;
}
.slide-enter-left {
  -webkit-animation-name: side;
          animation-name: side;
  -webkit-animation-duration: 2s;
          animation-duration: 2s;
  -webkit-animation-fill-mode: forwards;
          animation-fill-mode: forwards;
}
.menu-elements-cnt {
  margin-top: 30px;
  display: flex;
  flex-direction: column;
  height: 0;
  overflow: hidden;
  background-color: white;
}
.menu-links {
  display: inline-block;
  text-decoration: none;
  color: #212121;
  color: var(--main-color);
  font-weight: bolder;
  font-size: 1.2em;
  margin-bottom: 20px;
}

.up {
  -webkit-animation-name: ani-up;
          animation-name: ani-up;
  -webkit-animation-duration: 1s;
          animation-duration: 1s;
  -webkit-animation-fill-mode: forwards;
          animation-fill-mode: forwards;
}
.down {
  -webkit-animation-name: ani-down;
          animation-name: ani-down;
  -webkit-animation-duration: 1s;
          animation-duration: 1s;
  -webkit-animation-fill-mode: forwards;
          animation-fill-mode: forwards;
}

@-webkit-keyframes ani-up {
  0% {
    height: 0;
  }
  50% {
    height: 185px;
  }
  100% {
    height: 100%;
  }
}

@keyframes ani-up {
  0% {
    height: 0;
  }
  50% {
    height: 185px;
  }
  100% {
    height: 100%;
  }
}
@-webkit-keyframes ani-down {
  0% {
    height: auto;
  }
  50% {
    height: 185px;
  }
  100% {
    height: 0;
  }
}
@keyframes ani-down {
  0% {
    height: auto;
  }
  50% {
    height: 185px;
  }
  100% {
    height: 0;
  }
}

.header-cnt {
  margin-top: 15%;
}
.header-name {
  text-align: left;
}
.header-sub {
  text-align: left;
}

.content-cnt {
  text-align: left;
  margin: 10% 10%;
}
.content-text {
  text-align: left;
  font-weight: bold;
  line-height: 190%;
  margin-bottom: 5%;
}

.image {
  display: flex;
  flex-direction: column;
  margin-bottom: 10%;
}
.image-subtitle {
  font-size: 0.8em;
  font-weight: bold;
  color:lightgray;
}

.grayedBackround {
  padding: 5px 10px;
  border-radius: 1%;
  background-color: hsla(0, 0%, 93%, 0.575);
}

.code-cnt {
  margin: 0 auto;
  max-width: 800px;
  background-color: hsl(222, 11%, 18%);
}
.code {
  padding: 3%;
}

.center{
  margin: 0 auto;
}
.space-left{
  margin-left: 10%;
}

.bounceImage {
  -webkit-animation: bounceImage 3s infinite ease-in-out;
          animation: bounceImage 3s infinite ease-in-out;
}

@-webkit-keyframes bounceImage {
  0%,100% {
    padding-top: 0;
  }
  30%{
    padding-top: 30px;
  }
  70%{
    padding-top: 30px;
  }
}

@keyframes bounceImage {
  0%,100% {
    padding-top: 0;
  }
  30%{
    padding-top: 30px;
  }
  70%{
    padding-top: 30px;
  }
}

#landing-first-cnt {
  height: 100vh;
}
.landing-header-cnt h3 h1 {
  display: inline-block !important;
}
.landing-header-cnt {
  position: relative;
  margin: 5% auto 5%;
  flex-wrap: wrap;
  background: #e9e9e91e;
  border-radius: 20px;
  padding: 5%;
  max-width: 600px;
}
.expand {
  -webkit-animation-name: expand;
          animation-name: expand;
  -webkit-animation-duration: 2s;
          animation-duration: 2s;
  -webkit-animation-fill-mode: forwards;
          animation-fill-mode: forwards;
}
@-webkit-keyframes expand {
  0% {
    height: 0;
    width: 0;
  }
  50% {
    height: 75%;
    width: 75%;
  }
  100% {
    height: 100%;
    width: 100%;
  }
}
@keyframes expand {
  0% {
    height: 0;
    width: 0;
  }
  50% {
    height: 75%;
    width: 75%;
  }
  100% {
    height: 100%;
    width: 100%;
  }
}

.landing-header-image-cnt {
  border-radius: 50%;
  margin: auto 0;
  max-width: 200px;
  margin-right: 5%;
}
.profile-image {
  background: #ee0979; /* fallback for old browsers */ /* Chrome 10-25, Safari 5.1-6 */
  background: -webkit-gradient(
    linear,
    left top, right top,
    from(#ff6a00),
    to(#ee0979)
  );
  background: linear-gradient(
    to right,
    #ff6a00,
    #ee0979
  ); /* W3C, IE 10+/ Edge, Firefox 16+, Chrome 26+, Opera 12+, Safari 7+ */

  padding: 5px;
  margin: 0;
  max-width: 150px;
  height: auto;
}
#info-cnt {
  max-width: 1000px;
  margin: 0 auto;
  background-color: #e9e9e91e;
  padding: 5%;
  box-shadow: #2121212a 0px 1px 3px;
}
.landing-text {
  margin-left: 15%;
  margin-right: 15%;
  text-align: left;
  font-size: 1em;
}
#background-text {
  margin-top: -5%;
  margin-left: 15%;
  max-width: 60%;
}

.arrow-down {
  position: absolute;
  left: 95%;
  bottom: 5px;
  float: right;
  width: 0;
  height: 0;
  border-left: 20px solid transparent;
  border-right: 20px solid transparent;
  border-top: 20px solid #212121;
}

.bounce {
  -webkit-animation: bounce 2s infinite;
          animation: bounce 2s infinite;
}

@-webkit-keyframes bounce {
  0%,
  100% {
    bottom: 5vh;
  }
  25% {
    bottom: 6vh;
  }
  50% {
    bottom: 8vh;
  }
}

@keyframes bounce {
  0%,
  100% {
    bottom: 5vh;
  }
  25% {
    bottom: 6vh;
  }
  50% {
    bottom: 8vh;
  }
}

.flexRow {
  display: flex;
  flex-direction: row;
}
.flexColumn {
  display: flex;
  flex-direction: column;
}
.roundedImage {
  border-radius: 50%;
}
.lightBlue {
  color: #2196f3;
}
.lightRed {
  color: #f44336;
}
.lightOrange {
  color: #ff9800;
}

#graph-canvas {
  position: absolute;
  height: 100%;
  width: 100%;
  background-color: white;
  z-index: 4;
}

.projectsList-cnt{
  display: flex;
  flex-direction: column;
}
#project-date {
  display: block;
  font-size: 1.3em;
  text-align: right;
}
.project-cnt {
  display: inline-block;
  margin: 0 auto;
  position: relative;
}
#project-discription {
  max-width: 600px;
  padding-left: 10px;
  border-left: solid #212121 3px;
}
#viewProject-cnt {
  position: absolute;
  background-color: rgba(168, 159, 168, 0.0);
  top: 0;
  left: 0;
  width:100%;
  height: 100%;
}
#viewProject-text{
  position: absolute;
  top:45%;
  left:10%;
  padding: 10px;
}

.growRight{
  -webkit-animation: growRight 1s forwards ease-in-out;
          animation: growRight 1s forwards ease-in-out;
}
.slideRight {
  -webkit-animation: slideRight 1s forwards;
          animation: slideRight 1s forwards;
}

.flexColumn {
  display: flex;
  flex-direction: column;
}
.flexRow {
  display: flex;
  flex-direction: row;
}
.flexRowElem {
  margin-right: 5px;
}
.flexColumnElem {
  margin-bottom: 5px;
}

@-webkit-keyframes growRight {
  0% {
    width: 0;
  }
  100% {
    width: 100%;
  }
}

@keyframes growRight {
  0% {
    width: 0;
  }
  100% {
    width: 100%;
  }
}

@-webkit-keyframes slideRight {
  0% {
    margin-left: 0;
  }
  100% {
    margin-left: 30px;
  }
}

@keyframes slideRight {
  0% {
    margin-left: 0;
  }
  100% {
    margin-left: 30px;
  }
}

.hiddenLink {
  display: none;
}

